@import "partials/mixins";

@font-face {
	font-family: "ProximaNova-Regular";
	src: url("../fonts/ProximaNova/ProximaNova-Regular.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Regular";
	src: url("../fonts/Raleway/Raleway-Regular.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Bold";
	src: url("../fonts/ProximaNova/ProximaNova-Bold.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Bold";
	src: url("../fonts/Raleway/Raleway-Bold.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Light";
	src: url("../fonts/ProximaNova/ProximaNova-Light.ttf") format("truetype");
}

$desk-regular-proxima: "ProximaNova-Regular", sans-serif;
$desk-bold-proxima: "ProximaNova-Bold", sans-serif;
$desk-regular-raleway: "Raleway-Regular", sans-serif;
$desk-bold-raleway: "Raleway-Bold", sans-serif;
$desk-light-proxima: "ProximaNova-Light", sans-serif;
$Bghover-color: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
$arrowcolor: #E78B48;

body {
	width: 100%;
	height: 100vh;
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background: $Bghover-color;
	box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
	color: #fff;
	font-family: 'ProximaNova-Regular','ProximaNova-Bold','Raleway-Regular','Raleway-Bold', sans-serif;
	font-size: 14px;
	line-height: auto;
}

.error_message_container {
	width: 508px;
	height: 100%;
	position: relative;
	left: 0;
	right: 0;
	margin: auto;

	.error_message_container_title {
		height: 112px;
		width: 100%;
		margin-top: 189px;
		color: #FFFFFF;
		font-family: $desk-bold-raleway;
		font-size: 48px;
		font-weight: bold;
		line-height: 56px;
		text-align: center;
		display: inline-block;
	}

	.error_message_container_advice {
		height: 24px;
		width: 474px;
		color: #FFFFFF;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		font-weight: bold;
		line-height: 24px;
		display: inline-block;
		margin-top: 0px;
		margin-left: 21px;
		white-space: nowrap;
	}
}

.error_message_container a {
	display: block;
	margin-top: 100px;
	margin-left: 159px;
	height: 16px;
	width: 169px;
	color: #FFFFFF;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	text-decoration: none;
	position: relative;
	white-space: nowrap;
	cursor: pointer;
}

.error_message_container a::after {
	content: '';
	position: absolute;
	top: 0px;
	left: 180px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right_white.svg") no-repeat center;
}

@-moz-document url-prefix() {
	.error_message_container {
		width: 550px;
	}

	.error_message_container .error_message_container_title {
		width: 100%;
		height: 112px;
		text-align: unset;
	}

	.error_message_container a::after {
		left: 185px;
		top: 1px;
	}
}

